<script>
import 'lightgallery.js'
import 'lightgallery.js/dist/css/lightgallery.css'
import '../assets/style/main.css'

export default {
  name: 'Main',
  components: {
    'app-header': () => import('../components/Header.vue'),
    'app-footer': () => import('../components/Footer.vue'),
    'app-record': () => import('../components/Record.vue'),
  },
  data() {
    return {
      modalRecord: false,

      gallItemList: null,
      activeGall: 1,
      timer: null,
      TIME: 7000
    };
  },
  mounted() {
    this.init();
    this.initGall();
  },
  methods: {
    init() {
      window.lightGallery(this.$refs['lightgallery']);
    },
    openModalRecord() {
      this.modalRecord = true;
    },
    initGall() {
      this.gallItemList = this.$refs['gall'].querySelectorAll('.main__gall__item');
      this.gallItemList[this.activeGall - 1].classList.add('active');

      this.timer = setTimeout(() => {
        this.gallControl('right');
      }, this.TIME);
    },
    gallControl(direction) {
      if (!direction)
        return;

      clearTimeout(this.timer);
      
      if (direction == 'left') {
        this.activeGall = (this.activeGall - 1 <= 0) 
          ? this.gallItemList.length 
          : this.activeGall - 1;
      } else if (direction == 'right') {
        this.activeGall = (this.activeGall + 1 <= this.gallItemList.length) 
          ? this.activeGall + 1 
          : 1;
      }

      this.timer = setTimeout(() => {
        this.gallControl('right');
      }, this.TIME);

      this.$refs['gall'].querySelector('.active').classList.remove('active');
      this.gallItemList[this.activeGall - 1].classList.add('active');
    }
  }
}
</script>

